import TranslatableInput from '@components/translatable-input';
import ChoiceTree from '@js/components/form/choice-tree.js';

const $ = window.$;

$(() => {
    new TranslatableInput();
    new ChoiceTree('#carrousel_categories');
});

$(document).ready(function () {
    window.prestashop.component.initComponents(
        [
            'MultistoreConfigField',
            'Grid',
        ],
    );
    const contentBlockGrid = new window.prestashop.component.Grid('carrousel');
    contentBlockGrid.addExtension(new prestashop.component.GridExtensions.AsyncToggleColumnExtension());
});
